export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport {\n    DEMO_PATH_TOKEN,\n    NuiChipsModule,\n    NuiDocsModule,\n    NuiIconModule,\n    NuiPopoverModule,\n    SrlcStage,\n} from \"@nova-ui/bits\";\n\nimport {\n    AutohideChipsExampleComponent,\n    BasicChipsExampleComponent,\n    ChipsCustomCssExampleComponent,\n    ChipsDocsExampleComponent,\n    ChipsOverflowExampleComponent,\n    ChipsVisualTestComponent,\n    ChipsTestComponent,\n    GroupedChipsExampleComponent,\n    VerticalFlatChipsExampleComponent,\n    VerticalGroupedChipsExampleComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: ChipsDocsExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.beta,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"overflow\",\n        component: ChipsOverflowExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.beta,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"chips-visual-test\",\n        component: ChipsVisualTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"chips-test\",\n        component: ChipsTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"custom-css\",\n        component: ChipsCustomCssExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiChipsModule,\n        NuiPopoverModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n        NuiIconModule,\n    ],\n    declarations: [\n        AutohideChipsExampleComponent,\n        BasicChipsExampleComponent,\n        ChipsDocsExampleComponent,\n        ChipsVisualTestComponent,\n        ChipsTestComponent,\n        GroupedChipsExampleComponent,\n        VerticalFlatChipsExampleComponent,\n        VerticalGroupedChipsExampleComponent,\n        ChipsOverflowExampleComponent,\n        ChipsCustomCssExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class ChipsModule {\n}\n";