export default "import { Component, ViewEncapsulation } from \"@angular/core\";\nimport { IChipRemoved, IChipsItem, IChipsItemsSource } from \"@nova-ui/bits\";\nimport _pull from \"lodash/pull\";\n\n@Component({\n    selector: \"nui-chips-custom-css-example\",\n    templateUrl: \"./chips-custom-css.example.component.html\",\n    styleUrls: [\"chips-custom-css.example.component.less\"],\n    encapsulation: ViewEncapsulation.None,\n})\n\nexport class ChipsCustomCssExampleComponent {\n    public standaloneChips: Array<IChipsItem & any> = [\n        {\n            id: \"standaloneChip1\",\n            label: \"Custom Critical\",\n            customClass: [\"standalone-chip-custom-styles\", \"custom-chip-critical\"],\n            icon: \"severity_critical\",\n        },\n        {\n            id: \"standaloneChip2\",\n            label: \"Custom Warning\",\n            customClass: [\"standalone-chip-custom-styles\", \"custom-chip-warning\"],\n            icon: \"severity_warning\",\n        },\n        {\n            id: \"standaloneChip3\",\n            label: \"Custom Info\",\n            customClass: [\"standalone-chip-custom-styles\", \"custom-chip-info\"],\n            icon: \"severity_info\",\n        },\n        {\n            id: \"statusGroupItem5\",\n            label: \"Disabled\",\n            customClass: [\"standalone-chip-custom-styles\", \"custom-chip-disabled\"],\n            icon: \"severity_unknown\",\n        },\n    ];\n\n    public nuiChipsSet: IChipsItemsSource = {\n        flatItems: [\n            {\n                id: \"flatId2\",\n                label: \"Critical\",\n                customClass: \"nui-tag-critical nui-tag-with-hover-styles nui-tag-text-light\",\n            },\n            {\n                id: \"flatId3\",\n                label: \"Warning\",\n                customClass: \"nui-tag-warning nui-tag-with-hover-styles\",\n            },\n            {\n                id: \"flatId4\",\n                label: \"Info\",\n                customClass: \"nui-tag-info nui-tag-with-hover-styles nui-tag-text-light\",\n            },\n            {\n                id: \"flatId5\",\n                label: \"Ok\",\n                customClass: \"nui-tag-ok nui-tag-with-hover-styles nui-tag-text-light\",\n            },\n        ],\n        groupedItems: [\n            {\n                id: \"statusGroupId\",\n                label: \"Grouped Statuses\",\n                items: [\n                    {\n                        id: \"statusGroupItem2\",\n                        label: \"Critical\",\n                        customClass: \"nui-tag-critical nui-tag-with-hover-styles nui-tag-text-light\",\n                    },\n                    {\n                        id: \"statusGroupItem3\",\n                        label: \"Warning\",\n                        customClass: \"nui-tag-warning nui-tag-with-hover-styles\",\n                    },\n                    {\n                        id: \"statusGroupItem4\",\n                        label: \"Unknown\",\n                    },\n                    {\n                        id: \"statusGroupItem5\",\n                        label: \"Ok\",\n                        customClass: \"nui-tag-ok nui-tag-with-hover-styles nui-tag-text-light\",\n                    },\n                ],\n            },\n        ],\n    };\n\n\n    public widthChips: Array<IChipsItem & any> = [\n        {\n            id: \"widthChip1\",\n            label: \"Chip with default width and very very very very very very very long title \",\n        },\n        {\n            id: \"widthChip2\",\n            label: \"Chip with unset width and a very very very very very very very long title \",\n            customClass: \"unlimited-width\",\n        },\n    ];\n\n\n    public onClear(event: IChipRemoved) {\n        const source = event.group\n            ? this.nuiChipsSet.groupedItems?.find((group) => group.id === event.group?.id)?.items\n            : this.nuiChipsSet.flatItems;\n        _pull(source || [], event.item);\n    }\n\n    public onClearAll() {\n        this.nuiChipsSet.flatItems = [];\n        this.nuiChipsSet.groupedItems = [];\n    }\n\n}\n";